import React from 'react'
import {Link} from 'gatsby'
import {Button, Collapse, DropdownItem, DropdownToggle, DropdownMenu, Nav, Navbar, NavbarBrand, NavbarToggler, NavItem, NavLink, UncontrolledDropdown, 
   } from 'reactstrap'
import './Navigation.css'

class Navigation extends React.Component {
  state = {
    isOpen: false
  }

  toggle = () => {
    this.setState({ isOpen: !this.state.isOpen })
  }
  render(){
    return (
      <Navbar dark expand="lg" className="navigation">
        <NavbarBrand href="/"> Cinetimes </NavbarBrand>
        <NavbarToggler onClick={this.toggle} />
        <Collapse isOpen={this.state.isOpen} navbar>
          <Nav className="mr-auto" navbar>
            <NavItem>
              <NavLink href="/">Films</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/">Documentaires</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/">Dessins Animés</NavLink>
            </NavItem>
            <NavItem>
              <Link className="nav-link" to="/blog/">Blog</Link>
            </NavItem>
          </Nav>
          <Nav navbar>
            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret>
                Langue
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem>
                  Français 
                </DropdownItem>
                <DropdownItem>
                  Espagnol
                </DropdownItem>
                <DropdownItem>
                  Englais
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
          </Nav>
        </Collapse>
      </Navbar>
      )
  }
} 

export default Navigation