import React from "react"
import { graphql, Link } from 'gatsby' 
import Layout from '../components/Layout'
import Video from '../components/Video'
import "./movie.css"

const RelatedMovies = ({related}) => {
  let r = related.map(i => (
    <li className="media mb-3">
      <Link  to={i.slug} className="underline-not" title="">
        <img className="mr-3" width="168" height="95" src={`${process.env.GATSBY_API_URL}${i.cover}`} alt=""/>
      </Link>
      <div className="media-body">
        <Link  to={i.slug} className="underline-not" title="">
          <h5 className="mt-0 mb-1">
            {i.title}
          </h5>
          <p>
            author
          </p>
        </Link>

      </div>
    </li>
  ))
  return (
    <div className="col-lg-4 col-xs-12 sidebar">
      <div className="d-flex flex-column">
        <ul className="list-unstyled mt-3">

        {r}

        </ul>
      </div>
    </div>
  )
}


const CommentForm = () => (
  <div className="col-lg-8 col-xs-12 py-3 px-md-3 px-sm-1 mb-5 mx-md-3">
    <h3 class="text-center my-2 font-weight-bold mb-5">Laisser un commentaire</h3>

    <form method="POST" id="commentForm" action="https://api.staticman.net/v2/entry/cinetimes/cinetimes-fr/source/comments" >
      <div class="col-12 col-md-8 m-auto">
        <input name="options[slug]" type="hidden" value=""/>
        
        <div class="form-row">
          <div class="col">
            <label for="name"> Nom </label>
            <input name="fields[name]" type="text" id="name" class="form-control"/>
          </div>
          <div class="col">
            <label for="email">Email </label>
            <input name="fields[email]" id="email" type="email" class="form-control"/>
            <small id="" class="form-text text-muted">Ne seras jamais partagée</small>
          </div>
          <div style={{display: 'none'}}>
            <input type="phone" name="phone" id="phone"/>
          </div>
        </div>
    
        <div class="form-group">
          <label for="message">Commentaire</label>
          <textarea class="form-control" id="message" rows="4"name="fields[message]"></textarea>
        </div>
      
        
        <button type="submit" class="btn btn-success mt-2">Commenter</button>
      </div>		
    </form>
  </div>
)

export default ({data}) => {
  const title = data.internalMovies.title
  const synopsis = data.internalMovies.synopsis
  return (
    <Layout>
      <div className="container-fluid movie-page-container">
        <div className="row">
          <div className="col-lg-8 col-xs-12 pr-lg-0" id="video_et_description">
            
            {
              data.internalMovies.videos && <Video videos={data.internalMovies.videos}/>
            }
            

            <div className="videoDescription px-4 text-left d-flex justify-content-between">
              <h1 className="py-2 mb-0" id="video-title">{title}</h1>
              <div className="text-right my-auto">
                  <a href="/"> <i className="far fa-edit text-dark"></i> </a>
              </div>
            </div>

            <div className="videoDescription px-4 pb-2 text-left font-weight-light">
              <h1>Synopsis</h1>
              <p className="text-left"> 
                {synopsis}
              </p>
            </div>
          </div>

            <RelatedMovies related={data.internalMovies.recommendations}/>

            <div className="col-lg-8 col-xs-12 py-3 px-md-3 px-sm-1 mb-5 mx-3">
              <h2 class="text-center my-2 font-weight-bold mb-5"> 1 Commentaire</h2>
              <div class="d-flex flex-column col-12">
                <div class="mx-auto my-1 bg-white w-100 col-12 col-sm-9 rounded border border-white pt-3 px-3">
                  <span class="font-weight-bold"> Juliette </span>
                  <br/>
                  <p class="font-weight-light">Sur un thème difficile, Truffaut nous fait ici une démonstration de cinéma, rien de trop, que des petites touches. Ce film est l'œuvre d'un auteur hypersensible qui a l'intelligence de ne jamais tomber dans la sensiblerie, de choisir le plan qu'il faut et le faire durer le temps qu'il faut, du grand art. C'est beau, c'est intelligent, ça fait réfléchir et on en redemande. Merci Monsieur Truffaut ! </p>
                </div>
              </div>
            </div>

            
              <CommentForm/>

        </div>
      </div>
    </Layout>
  )
}

export const query = graphql`
query($slug: String!) {
  internalMovies(slug: { eq: $slug }){
    id
    title
    slug
    language
    synopsis
    poster
    videos {
      url
      source
      audio
      subtitles
      type
      movie
    }
    recommendations{
      title
      author
      slug
      cover
    }
    movie {
      alternative_id
      original_title
      original_language
      original_country
      budget
      box_office
      runtime
      release
      genres
    }
  }
}
`