import React from 'react'
import PropTypes from 'prop-types'
import Plyr from 'react-plyr'
import 'plyr/dist/plyr.css'


class Video extends React.Component {
  static propTypes = {
    videos: PropTypes.array
  }

  state = {

  }

  render() {
    if(!this.props.videos) return ''
    if(this.props.videos.length === 0) return ''

    let video = []
    const videos = this.props.videos.map(i => {
      console.log(i.url)
      if (i.source === 'archive') {
        video.push(<Archive url={i.url} />)
      } else if(i.source === 'youtube') {
        let id = i.url.split('?v=')[1];
        if (id && id.includes('&')) {
          id = id.split('&')[0]
        }
        video.push(<Youtube id={id} />)
      } else if(i.source === 'dailymotion') {
        video.push(<DailyMotion url={i.url} />)
      } else if(i.source === 'vimeo') {
        let id = i.url.split('vimeo.com/')[1]
        video.push(<Vimeo id={id} />)
      }
    })
    return video[0]
  }
}

export default Video

const VideoSizeContainer = ({children}) => (
  <div className="tab-content" id="nav-tabContent">
    <div className="tab-pane fade show active" id="nav-archive" role="tabpanel" aria-labelledby="nav-archive-tab">
      {children}
    </div>
  </div> 
)

const Youtube = ({id}) => (
    <Plyr
      type="youtube" // or "vimeo"
      videoId={id}
    />
)
const Archive = ({url}) => (
  <div className="tab-content" id="nav-tabContent">
    <div className="tab-pane fade show active" id="nav-archive" role="tabpanel" aria-labelledby="nav-archive-tab">
     <iframe src={url} webkitallowfullscreen="true" mozallowfullscreen="true" allowfullscreen></iframe>
    </div>
  </div> 
)
const Vimeo = ({id}) => (
  <VideoSizeContainer>
    <Plyr type="vimeo" videoId={id} />
  </VideoSizeContainer>
)

const DailyMotion = ({url}) => (
  <div className="tab-content" id="nav-tabContent">
    <div className="tab-pane fade show active" id="nav-archive" role="tabpanel" aria-labelledby="nav-archive-tab">
      <iframe src={url} allowFullScreen allow="autoplay" frameborder="0"></iframe>
    </div>
  </div> 
)